$background-color: #deb887;
$light-background-color: #e0c8a8;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: $background-color;
    font-size: 2rem;
}

#quote-box {
    width: 100vw;
    height: 100vh;
    display: grid;
    place-items: center;
}

#quote-container {
    max-width: 650px;
    padding: 35px;
    background-color: white;
    border-radius: 15px;
    text-align: center;
    #quotation {
        margin-right: 13px;
    }
    #author {
        font-size: 1.25rem;
        text-align: right;
    }
    
}

#new-quote {
    background-color: $background-color;
    border-radius: 15px;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    &:hover {
        background-color: $light-background-color;
    }
}

#buttons-container {
    display: flex;
    justify-content: space-between;
    margin-top: 45px;
    align-items: flex-end;
    font-size: 42px;
    a:link,
    a:visited {
        color: $background-color;
        text-align: center;
        text-decoration: none;
        display: inline-block;
    }
}
